body, html {
  margin: 0;
  padding: 0;
  height: 100%;
}

.main-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 100vw;
}
.content {
  text-align: justify;
  line-height: 1.6;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 background-color: rgb(8, 11, 11);
}
.video-container {
  
  width: 100vw;
  height: 75vh;
  z-index: -1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
}

.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.activitiees {
  display: flex;
  justify-content: space-around;
  margin: 20px 0;
  background-color: #ffffff; /* Set the background color to white */
  padding: 20px; /* Add padding for spacing inside the activities section */
}

.activity {
  text-align: center;
  
}
.grey-rectangle {
  background-color: #333;
  color: rgb(94, 149, 0);
  padding: 20px;
  text-align: center;
  width: 100%;
}
.grey-rectangle p {
  color: #ffe8e8;
}

.activity h3 {
  margin-bottom: 10px;
  font-size: 1.5em;
  color: rgb(0, 0, 0);
}

.activity-images img {
  width: 300px;
  height: 300px;
  margin: 5px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid black;
  display: block;
  transition: all 0.3s ease; /* Add a smooth transition effect */
}

.activity-images img:hover {
  transform: scale(1.2); /* Scale the image slightly larger on hover */
}




.imported-image-container {
  text-align: center;
  margin-top: 20px;
  height: 800px;
  
}

.imported-image {
  width: 700px; /* Adjust the width as needed */
  height:auto;
  border-radius: 10px; /* Optional: Add border-radius for rounded corners */
  
}


.hero-section2 {
  width: 100%;
  height: 40vh;
  background-image: url('../images/aact9.jpg'); 
  background-size: cover; 
  background-position: center; 
  display: flex;
  justify-content: center;
  align-items: center;
  
  margin-left: 30px;
}

.hero-text2 h1 {
  font-size: 36px;
  color: #010101;
  text-shadow: 2px 2px 4px rgba(232, 226, 226, 0.7);
}





.info-container {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
  margin-bottom: 300px;
}

.info-box {
  text-align: center;
  height: 80px;
  width: 200px;
  margin: 30px;
}
.info-box2 {
  text-align: center;
  height: 100px;
  width: 300px;
  margin: 30px;
}

.logo {
  width: 80px; /* Adjust the size of the logos as needed */
  height: 80px;
  margin-bottom: 10px;
}

.image-container {
  width: 100vw;
  height: 60vh;
  z-index: -1;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Ensure the image doesn't overflow its container */
  position: relative;
  padding-top: 150px;
}

.background-image {
  width: 33%;
  height: 100%;
  object-fit: cover;
  margin-right: 10px;
}
.overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
}

.image-text {
  font-size: 2.5em;
  margin-bottom: 10px;
  color: #0c0b0b;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -650%);
  font-weight: bold;
}


.button {
  padding: 10px 20px;
  font-size: 1em;
  background-color: #333;
  color: white;
  border: none;
  cursor: pointer;
  text-decoration: none;
}
